import { ConfigPrintComponent } from './components/config-print/config-print.component';
import { ConfigSalesComponent } from './components/config-sales/config-sales.component';
import { AccountInformationComponent } from './components/account-information/account-information.component';
import { LottieModule } from 'ngx-lottie';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common'; 
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { SaleDesktopComponent } from './sale-desktop.component';
import { playerFactory } from 'src/app/app.module';
import { UserGuideComponent } from './user-guide/user-guide.component';
import { SalesDesktopBillType1Module } from '../components/bill-type/bill-type-1/sales-desktop/sales-desktop-bill-type1.module';
import { AddComponent } from './add/add.component';
import { DetailInvoiceComponent } from './detail-invoice/detail-invoice.component';
import { EditComponent } from './edit/edit.component';

const routes: Routes = [
  {
    path: '',
    component: SaleDesktopComponent
  },
  {
    path: 'add',
    component: AddComponent
  }, 
  {
    path: 'edit-sales',
    component: EditComponent
  }, 
  {
    path: 'detail-invoice',
    component: DetailInvoiceComponent
  }, 
]

@NgModule({
  declarations: [SaleDesktopComponent,AccountInformationComponent,ConfigSalesComponent,ConfigPrintComponent,UserGuideComponent,AddComponent,DetailInvoiceComponent, EditComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    ReactiveFormsModule,
    FormsModule,
    TranslateModule,
    LottieModule.forRoot({ player: playerFactory }),
    SalesDesktopBillType1Module
  ]
})
export class SaleDesktopModule { }
